import React from 'react'
import styled from 'styled-components'

import { Link } from 'gatsby'
import siteLogo from '../../images/Envy-Forge-Text-Logo.png'

const HeaderWrapper = styled.div`
  grid-area: header;
  padding: 1rem 0 0.75rem 2rem;
  font-size: 1.4em;
  background: rgb(0, 97, 158);
  color: white;
  img {
    margin-bottom: 0;
  }
`
const Header = () => (
  <HeaderWrapper>
    <Link
      to="/"
      style={{
        color: 'white',
        textDecoration: 'none',
      }}
    >
      <img
        style={{
          height: '40px',
        }}
        src={siteLogo}
        alt="Matt Ortiz Logo"
      />
    </Link>
  </HeaderWrapper>
)

export default Header
